    "Portugese Sample":["pt","O técnico Tite comandará nesta segunda-feira o primeiro treinamento da Seleção Brasileira visando as partidas contra o Uruguai e Paraguai, nos próximos dias 23 e 28, válidos pela Eliminatórias Sul-Americanas para a Copa da Rússia em 2018. Para esta primeira atividade, o comandante terá apenas 14 dos 23 convocados à disposição. Os demais jogadores chegarão entre a tarde desta segunda-feira e a manhã da terça-feira.   A atividade, que será realizado às 16h (de Brasília)  no CT do Corinthians, contará com as presenças de Marcelo, Casemiro, Ederson, Weverton, Diego, Diego Souza, Fagner, Dudu, Filipe Luís, Willian, Miranda, Paulinho, Gil e Renato Augusto - os dois últimos já treinaram no CT do São Paulo durante a semana passada.   Na chegada ao hotel onde está concentrada a Seleção Brasileira, o meia Renato Augusto, atualmente no Beijing Guoan, explicou quais serão as maiores dificuldades encontradas no duelo contra os uruguaios, fora de casa. 'Será um clima semelhante ao da Libertadores, jogaremos fora de casa, enfrentaremos uma catimba natural, mas não devemos entrar no jogo deles, temos que ficar com a cabeça no lugar e fazer o nosso jogo para poder vencer', analisou o titular.   Tite só terá todos os seus convocados no treino desta terça-feira, que será realizado no CT do São Paulo. Dos nove atletas que ainda irão se juntar aos outros convocados, seis ainda deverão chegar nesta segunda-feira, são eles Marquinhos, Thiago Silva, Fernandinho, Giuliano, Coutinho e Firmino. A convocação estará completa apenas nesta terça-feira, com a chegadas de Neymar, Daniel Alves e Alisson.   Líder das Eliminatórias Sul-Americanas com 27 pontos, a Seleção Brasileira encara o segundo colocado, Uruguai, com 23 pontos, na próxima quinta-feira, às 20h (de Brasília), no estádio Centenário, em Montevidéu. Já no dia 2 de março, a equipe volta a campo para enfrentar o Paraguai, no Estádio de Itaquera, às 21h45."],
    "French Sample":["fr","La méfiance envers l'Islam, jusque-là uniquement associée à l'extrême droite et d'une partie de la droite, ou plus récemment comme un marqueur réactionnaire dans le débat culturel, serait à présent largement et profondément partagée à gauche. Selon un sondage Ifop réalisé du 14 au 18 avril 2016 pour Le Figaro, 52% des électeurs socialistes considèrent que la place de l'islam est trop importante. En 2010, ils n'étaient que 39%. Une défiance qui monte en puissance et illustre un rejet global de la religion musulmane en France.  En cause, les multiples attaques terroristes dont a été victime l'Hexagone en 2015. A titre de comparaison, un sondage de 1989 révélait que 33% des Français étaient par exemple favorables à la construction des mosquées contre seulement 13% aujourd'hui. À l'époque, 31% des sondés étaient opposés au port du voile. En 2016, ils sont 63%.  En Allemagne, ce phénomène de rejet est moins puissant mais la donne a tout de même changé à la suite des événements du 31 décembre de Cologne. Alors que ces deux pays ont des histoires de l'immigration très différentes, analyse Jerôme Fourquet, le Directeur du Département Opinion de l'Ifop, cette convergence montre que les défis de ces questions majeures sont posés de manière similaire à toute la société occidentale."],
