    "O sorriso, meus amigos, é muito mais do que estas pobres definições, e eu pasmo ao imaginar o autor do dicionário no acto de escrever o seu verbete, assim a frio, como se nunca tivesse sorrido na vida. Por aqui se vê até que ponto o que as pessoas fazem pode diferir do que dizem. Caio em completo devaneio e ponho-me a sonhar um dicionário que desse precisamente, exatamente, o sentido das palavras e transformasse em fio-de-prumo a rede em que, na prática de todos os dias, elas nos envolvem. Não há dois sorrisos iguais. Temos o sorriso de troça, o sorriso superior e o seu contrário humilde, o de ternura, o de cepticismo, o amargo e o irónico, o sorriso de esperança, o de condescendência, o deslumbrado, o de embaraço, e (por que não?) o de quem morre. E há muitos mais. Mas nenhum deles é o Sorriso. O Sorriso (este, com maiúsculas) vem sempre de longe. É a manifestação de uma sabedoria profunda, não tem nada que ver com as contracções musculares e não cabe numa definição de dicionário. Principia por um leve mover de rosto, às vezes hesitante, por um frémito interior que nasce nas mais secretas camadas do ser. Se move músculos é porque não tem outra maneira de exprimir-se. Mas não terá? Não conhecemos nós sorrisos que são rápidos clarões, como esse brilho súbito e inexplicável que soltam os peixes nas águas fundas? Quando a luz do sol passa sobre os campos ao sabor do vento e da nuvem, que foi que na terra se moveu? E contudo era um sorriso."
